size: 14px;\n        line-height: 16px;\n}\n.tags-div[data-v-f6a3b7ac] {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        padding: 14px 24px;\n        display: flex;\n        align-items: center;\n}\n.tags-title[data-v-f6a3b7ac] {\n        font-weight: 500;\n        font-size: 16px;\n        line-height: 19px;\n        color: #757575;\n        margin-right: 24px;\n}\n.tag-name[data-v-f6a3b7ac] {\n        padding: 5.5px 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 4px;\n        margin-right: 8px;\n        font-size: 14px;\n        color: #2F3160;\n        white-space: nowrap;\n        font-weight: 500;\n        cursor: pointer;\n}\n.manage-org-div[data-v-f6a3b7ac] {\n        width: 77%; \n        display: flex; \n        justify-content: end;\n        position: absolute;\n        top: 80px;\n        right: 36px;\n}\n.manage-btn[data-v-f6a3b7ac] {\n        border: 1px solid #151233;\n        border-radius: 4px;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        color: #151233;\n        display: flex;\n        align-items: center;\n        font-weight: 500;\n}\n.delete-org-btn[data-v-f6a3b7ac] {\n        background: #CBCBCB;\n        border-radius: 4px;\n        color: #212121;\n        border: none;\n        margin-left: 12px;\n        display: flex;\n        align-items: center;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        font-weight: 500;\n}\n.view-btn[data-v-f6a3b7ac] {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\ntd[data-v-f6a3b7ac]:hover {\n        background-color: none !important;\n}\n\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/backoffice/organization.vue"],"names":[],"mappings":";AAuOI;QACI,yBAAyB;QACzB,8CAA8C;QAC9C,mBAAmB;QACnB,mBAAmB;QACnB,kBAAkB;QAClB,uBAAuB;QACvB,aAAa;QACb,sBAAsB;AAC1B;AACA;QACI,eAAe;AACnB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,mBAAmB;QACnB,gBAAgB;QAChB,OAAO;AACX;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;AAClB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,mBAAmB;AACvB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,8CAA8C;QAC9C,mBAAmB;QACnB,WAAW;AACf;AACA;QACI,iCAAiC;AACrC;AACA;QACI,yBAAyB;AAC7B;AACA;;QAEI,gCAAgC;QAChC;AACJ;AACA;QACI,gCAAgC;QAChC,kBAAkB;QAClB,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,gBAAgB;QAChB,WAAW;QACX,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,8CAA8C;QAC9C,mBAAmB;QACnB,kBAAkB;QAClB,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,kBAAkB;AACtB;AACA;QACI,kBAAkB;QAClB,mBAAmB;QACnB,aAAa;QACb,uBAAuB;QACvB,kBAAkB;QAClB,iBAAiB;QACjB,eAAe;QACf,cAAc;QACd,mBAAmB;QACnB,gBAAgB;QAChB,eAAe;AACnB;AACA;QACI,UAAU;QACV,aAAa;QACb,oBAAoB;QACpB,kBAAkB;QAClB,SAAS;QACT,WAAW;AACf;AACA;QACI,yBAAyB;QACzB,kBAAkB;QAClB,mBAAmB;QACnB,eAAe;QACf,cAAc;QACd,aAAa;QACb,mBAAmB;QACnB,gBAAgB;AACpB;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,cAAc;QACd,YAAY;QACZ,iBAAiB;QACjB,aAAa;QACb,mBAAmB;QACnB,mBAAmB;QACnB,eAAe;QACf,gBAAgB;AACpB;AACA;QACI,gBAAgB;QAChB,WAAW;QACX,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AAEA;QACI,iCAAiC;AACrC","sourcesContent":["<script setup lang=\"ts\">\n    import { ref, defineAsyncComponent, onBeforeMount, computed, watchEffect } from \"vue\";\n    import { container } from \"../../../../domain/core/diContainer\";\n    import { IAzureResourceGroupUrls, IBackofficeUseCase, IOrganizationUIExtended } from \"../../../../domain/useCase/IBackofficeUseCase\";\n    import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n    import { IRouter } from \"../../../../domain/presentation/IRouter\";\n    import { IOrganization } from \"../../../../../../common/qCloudTemp/membership\";\n    import { IDialog } from \"../../../../domain/presentation/IDialog\";\n    import { IUser_SUSI } from \"../../../../../../common/qCloudTemp/authentication\";\n    import { IApplication } from \"../../../../../../common/qCloudTemp/quickCloud\";\n    import { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n    import UserList from \"../../components/backoffice/user/userList.vue\";\n    import AppList from \"../../components/backoffice/application/applicationList.vue\";\n    import { ITableMethods } from \"../../../../domain/useCase/ITableMethods\";\n\n\n    const router = container.resolve<IRouter>(IRouter);\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n    const tableMethods = container.resolve<ITableMethods>(ITableMethods);\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n\n\n    const routerParams = router.getRouteParams<{ organizationId: string }>();\n    backofficeUseCase.setBreadcrumb([\"Organizations\", \"Organization Detail\"]);\n    const organization = ref<IOrganizationUIExtended>();\n    const dialog = container.resolve<IDialog>(IDialog);\n    const userList = ref<Array<IUser_SUSI>>([]);\n    const appList = ref<Array<IApplication>>([]);\n    const rgUrls = ref<IAzureResourceGroupUrls>();\n    const orgTags = ref<Array<{id: string, name: string}>>([]);\n    const usersDataIsReady = ref(false);\n    const appDataIsReady = ref(false);\n    const organizationGroupName = ref(\"\");\n    \n\n    onBeforeMount(async () => {\n        await getOrganizationInfo();\n        await listTags()\n        getTagData();\n        listOrganizationUsers();\n        listOrganizationApps();\n        getResourceGroupUrls();\n        getOrgGroupName();\n    }) \n\n    async function getOrganizationInfo() {\n        await executor.execute(async () => {\n            organization.value = await backofficeUseCase.getOrganizationInfo(routerParams.organizationId);\n        }, { loading: true });  \n    }\n\n\n    function openManageOrganizationDialog () {\n        const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n            if(reason == \"component\") {\n                getTagData();\n            }\n        }\n        dialog.showDialog(\n                defineAsyncComponent(() => import(`../../components/dialogs/backoffice/manageOrganization.vue`)),\n                { closable: true, title: \"Manage Organization\", width: \"592px\", maxHeight: \"650px\", closeCb },{organization: organization.value}\n            );\n        }\n    function openDeleteOrganizationDialog () {\n    dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/backoffice/deleteConfirmation.vue`)),\n            { closable: true, title: \"Delete Organization\", width: \"577px\", maxHeight: \"206px\" },\n            { \n                infoText: 'Organization Name',  \n                placeholder:`Write organization name '${organization.value?.name}' to delete`, validText: organization.value?.name, \n                deleteCb: async () => {\n                    await executor.execute(async () => {\n                        await backofficeUseCase.deleteOrganization(organization.value?.id!, organization.value?.name!);\n                        router.push(\"/backoffice/organizations\");\n                    }, { loading: true });\n                }\n            }\n        );\n    }\n    function listOrganizationUsers() {\n        const organizationId = routerParams.organizationId;\n        executor.execute(async () => {\n            userList.value = await backofficeUseCase.listOrganizationUsers(organizationId);\n            usersDataIsReady.value = true;\n        }, { loading: true });\n    }\n    function listOrganizationApps() {\n        const organizationId = routerParams.organizationId;\n        executor.execute(async () => {\n            appList.value = await backofficeUseCase.listOrganizationApps(organizationId);\n            appDataIsReady.value = true;\n        }, { loading: true });\n    }\n\n    function getResourceGroupUrls() {\n        executor.execute(async () => {\n            if (!organization.value?.cloud?.azure?.resourceGroupID) {\n                console.log(`Organization '${organization.value!.id} - ${organization.value!.name}' has no \"cloud.azure.resourceGroupID\"!`)\n                return;\n            }\n            rgUrls.value = await backofficeUseCase.getAzureResourceGroupUrls(organization.value.cloud.azure.resourceGroupID!);\n        }, { loading: true });\n    }\n    function openResourceGroupOverviewPage() {\n        window.open(rgUrls.value!.overviewUrl, '_blank');\n    }\n    function openResourceGroupCostPage() {\n        window.open(rgUrls.value!.costUrl, '_blank');\n    }\n    async function listTags() {\n        await executor.execute( async () => {\n            await backofficeUseCase.listTags();\n        }, {loading: true})\n    }\n    function getTagData() {\n        executor.execute(async () => {\n            await getOrganizationInfo();\n        }, { loading: true });\n        const tagsDict: Record<string, string> = {};\n        orgTags.value = [];\n        viewModel.backoffice?.tags?.forEach(tag => tagsDict[tag.ID] = tag.name);\n        organization.value?.tags?.forEach(tagID => {\n            const tagData = {id: tagID, name: tagsDict[tagID]};\n            orgTags.value?.push(tagData);\n        })\n    }\n    function viewTag(tagId: string) {\n        router.push(`/backoffice/tag/${tagId}`);\n    }\n    function getOrgGroupName() {\n        executor.execute(async ()  => {\n            if(organization.value!.organizationGroupId) {\n                const orgGroupName = await backofficeUseCase.getOrgGroupNameByOrgID(organization.value!.id);\n                if(orgGroupName) {\n                    organizationGroupName.value = orgGroupName;\n                }\n                else {\n                    organizationGroupName.value = \"No Licensing Group\"\n                }\n            }\n            else {\n                organizationGroupName.value = \"No Licensing Group\"\n            }\n        },{loading: true})\n    }\n</script>\n\n<template>\n    <div class=\"manage-org-div\">\n        <button class=\"manage-btn\" @click=\"openManageOrganizationDialog()\"><i class=\"mdi mdi-cog\" style=\"font-size: 20px; margin-right: 4px\"></i><span>MANAGE ORGANIZATION</span></button>\n        <button class=\"delete-org-btn\" @click=\"openDeleteOrganizationDialog()\"><i class=\"mdi mdi-delete\" style=\"font-size: 20px; margin-right: 4px\"></i><span>DELETE</span></button>\n    </div>\n    <div style=\"display:flex; margin-bottom: 32px;\">\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Name</div>\n            <div class=\"organization-info\">{{ organization?.name }}</div>\n        </div>\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Users</div>\n            <div class=\"organization-info\">{{ userList.length }}</div>\n        </div>\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Apps</div>\n            <div class=\"organization-info\">{{ appList.length }}</div>\n        </div>\n        <div class=\"organization-info-type\" style=\"flex: 1;\">\n            <div class=\"organization-info-type-name\">Date Created</div>\n            <div class=\"organization-info\">{{ tableMethods.formatDate(organization?.createDate) }}</div>\n        </div>\n        <div class=\"organization-info-type\" style=\"flex: 1;\">\n            <div class=\"organization-info-type-name\">ID</div>\n            <div class=\"organization-info\" style=\"font-size: 20px;\">{{ organization?.id }}</div>\n        </div>\n        <!-- <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">DB Name</div>\n            <div class=\"organization-info\">{{ organization?.dbName }}</div>\n        </div> -->\n        <div class=\"organization-info-type\">\n            <div class=\"organization-info-type-name\">Licensing Group</div>\n            <div class=\"organization-info\">{{ organizationGroupName }}</div>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div class=\"tags-div\">\n            <span class=\"tags-title\">Tags</span>\n            <span v-for=\"tag in orgTags\" :key=\"tag.id\" class=\"tag-name\" @click=\"viewTag(tag.id)\">{{ tag.name }}</span>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div v-if=\"usersDataIsReady\" >\n            <UserList :users=\"userList\"></UserList>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div v-if=\"appDataIsReady\" >\n            <AppList :apps=\"appList\"></AppList>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div class=\"div-title\">Azure</div>\n        <div>\n            <table class=\"azure-info-table\">\n                <tr>\n                    <td class=\"thead-azure col-xl-4\">resourceGroupId</td>\n                    <td v-if = \"organization?.cloud?.azure?.resourceGroupID \" class=\"col-xl-7\">{{ organization?.cloud?.azure?.resourceGroupID }}</td>\n                    <td v-else >-</td>\n                    <td class=\"col-xl-1\"><div><button v-if=\"rgUrls?.overviewUrl\" class=\"view-btn\" @click=\"openResourceGroupOverviewPage\">VIEW</button></div></td>\n                </tr>\n                <tr>\n                    <td class=\"thead-azure\">modelStorageID</td>\n                    <td v-if=\" organization?.cloud?.azure?.modelStorageID \">{{ organization?.cloud?.azure?.modelStorageID }}</td>\n                    <td v-else >-</td>\n                    <td></td>\n                </tr>\n                <tr>\n                    <td class=\"thead-azure\">cost</td>\n                    <td>-</td>\n                    <td><div><button v-if=\"rgUrls?.costUrl\" class=\"view-btn\" @click=\"openResourceGroupCostPage\">VIEW</button></div></td>\n                </tr>\n                <tr>\n                    <td class=\"thead-azure\">costDate</td>\n                    <td>-</td>\n                    <td></td>\n                </tr>\n            </table>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n    .organization-info-type {\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        background: #FFFFFF;\n        margin-right: 37px;\n        padding: 24px 24px 18px;\n        display: flex;\n        flex-direction: column;\n    }\n    .organization-info-type:last-child {\n        margin-right: 0;\n    }\n    .organization-info-type-name {\n        font-weight: 500;\n        font-size: 20px;\n        line-height: 23px;\n        color: #757575;\n        margin-bottom: 20px;\n        min-width: 100px;\n        flex: 1;\n    }\n    .organization-info {\n        font-weight: 500;\n        font-size: 28px;\n        line-height: 33px;\n        color: #151233; \n    }\n    .div-title {\n        font-weight: 500;\n        font-size: 24px;\n        line-height: 28px;\n        color: #151233;\n        margin-bottom: 20px;\n    }\n    .azure-info-table {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        width: 100%;\n    }\n    td:hover {\n        background-color: none !important;\n    }\n    .azure-info-table tr:hover {\n        background-color: #EEEEEE;\n    }\n    .azure-info-table th\n        {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 18px 36px\n    }\n    .azure-info-table td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 20px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .azure-info-table .thead-azure {\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .view-btn {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .tags-div {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        padding: 14px 24px;\n        display: flex;\n        align-items: center;\n    }\n    .tags-title {\n        font-weight: 500;\n        font-size: 16px;\n        line-height: 19px;\n        color: #757575;\n        margin-right: 24px;\n    }\n    .tag-name {\n        padding: 5.5px 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 4px;\n        margin-right: 8px;\n        font-size: 14px;\n        color: #2F3160;\n        white-space: nowrap;\n        font-weight: 500;\n        cursor: pointer;\n    }\n    .manage-org-div {\n        width: 77%; \n        display: flex; \n        justify-content: end;\n        position: absolute;\n        top: 80px;\n        right: 36px;\n    }\n    .manage-btn {\n        border: 1px solid #151233;\n        border-radius: 4px;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        color: #151233;\n        display: flex;\n        align-items: center;\n        font-weight: 500;\n    }\n    .delete-org-btn {\n        background: #CBCBCB;\n        border-radius: 4px;\n        color: #212121;\n        border: none;\n        margin-left: 12px;\n        display: flex;\n        align-items: center;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        font-weight: 500;   \n    }\n    .view-btn {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n\n    td:hover {\n        background-color: none !important;\n    }\n\n\n</style>\n"],"sourceRoot":""}]);
